import axios from 'axios'
import qs from 'qs'

axios.defaults.timeout = 5000;// 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';// 
axios.defaults.baseURL = process.env.NODE_ENV !== 'production' ? '' : 'http://106.12.6.104:8080/';// 
// POST()
axios.interceptors.request.use((config) => {
	// 
	if(config.method === 'post'){
		// config.data = qs.stringify(config.data);
		if(config.headers['Content-Type'] !== 'application/x-www-form-urlencoded'){
			config.data = qs.stringify(config.data);
		}else{
			config.data = config.data;
		}
	}
	if(config.headers['accessToken'] !== undefined){
		axios.defaults.headers.post['accessToken'] = config.headers['accessToken']
	}
	return config;
},(error) => {
	return Promise.reject(error);
});

// ()
axios.interceptors.response.use((res) => {
	// 
	if(!res.data.success){
		return Promise.resolve(res);
	}
	return res;
}, (error) => {
	return Promise.reject(error);
});

/**
 * Promise(post)
 * @param url String 
 * @param params Object {}
 * @param headers Object http
 * @returns {Promise<any>}
 */
export function fetchPost(url, params,headers) {
	if(headers !== undefined){
		const { accessToken } = headers;
		axios.defaults.headers.post['accessToken'] = accessToken
	}
	return new Promise((resolve, reject) => {
		axios.post(url, params)
			.then(response => {
				resolve(response);
			}, err => {
				reject(err);
			})
			.catch((error) => {
				reject(error)
			})
	})
}

/**
 * Promise(GET)
 * @param url String 
 * @param param Object {}
 * @param headers Object http
 * @returns {Promise<any>}
 */
export function fetchGet(url, param,headers) {
	if(headers !== undefined){
		const { accessToken } = headers;
		axios.defaults.headers.get['accessToken'] = accessToken
	}
	return new Promise((resolve, reject) => {
		axios.get(url, { params : param })
			.then(response => {
				resolve(response)
			}, err => {
				reject(err)
			})
			.catch((error) => {
				reject(error)
			})
	})
}

/**
 * 
 * @param url String 
 * @param files Array 
 * @returns {Promise<any>}
 */
export function postFile(url,files) {
	let instance = axios.create();
	instance.defaults.timeout = 30000;
	let forms = new FormData()
	for (let i = 0; i < files.length; i++) {
		forms.append('file',files[i].file)
	}
	instance.defaults.headers.post['Content-Type'] = 'multipart/form-data';
	return new Promise((resolve, reject) => {
		instance.post(url, forms)
		.then(response => {
			resolve(response);
		}, err => {
			reject(err);
		})
		.catch((error) => {
			reject(error)
		})
	})
}
export default {
	fetchPost,
	fetchGet,
	postFile
}
